import React, { Component } from 'react';
import Logo from '../Logo/logo.js';

class HomeNav extends Component {
  constructor() {
    super();
    this.state= {
      width: window.innerWidth,
    };
  }
  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  // make sure to remove the listener
  // when the component is not mounted anymore
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange= () => {
    this.setState({ width: window.innerWidth });
  };

  render() {
    const { width }= this.state;
    const isMobile= width < 1200;
    // the rest is the same...

    if (isMobile) {

      return (
        <React.Fragment>
          <Logo />
{/*
          <div className="left-trees">
            <img src={displaynavmob} className="trees tree-1" data-id="display" alt="Display Admin Project" />
            <img src={tagsnavmob} className="trees tree-2" data-id="tags" alt="Digital Pricetag Project" />
            <img src={mapsnavmob} className="trees tree-3" data-id="maps" alt="Store Map Project" />
          </div>
          <div className="right-trees">
            <img src={ocflnavmob} className="trees tree-4" data-id="ocfl" alt="OCFL Projects" />
            <img src={phpnavmob} className="trees tree-5" data-id="php" alt="PHP Plugins Project" />
            <img src={anynavmob} className="trees tree-6" data-id="any" alt="Any Decision Project" />
          </div>
*/}
        </React.Fragment>
      );
    } else {
      return (
        <React.Fragment>
{/*
          <div className="left-trees">
            <img src={displaynav} className="trees tree-1" data-id="display" alt="Display Admin Project" />
            <img src={tagsnav} className="trees tree-2" data-id="tags" alt="Digital Pricetag Project" />
            <img src={mapsnav} className="trees tree-3" data-id="maps" alt="Store Map Project" />
          </div>
*/}
              <Logo />
{/*
          <div className="right-trees">
            <img src={ocflnav} className="trees tree-4" data-id="ocfl" alt="OCFL Projects" />
            <img src={phpnav} className="trees tree-5" data-id="php" alt="PHP Plugins Project" />
            <img src={anynav} className="trees tree-6" data-id="any" alt="Any Decision Project" />
          </div>
*/}
        </React.Fragment>
      );

    }
  }
}

export default HomeNav;
