import React, { Component } from 'react';
import ReactGA from 'react-ga';

import HomeNav from './components/HomeNav/homenav.js';
import Social from './components/SocialLinks/social.js';
import Resume from './components/Resume/resume.js';
import ProjectFilter from './components/Projects/projectFilter.js';
import './App.css';

const trackingId = "UA-89547130-1"; // Replace with your Google Analytics tracking ID
ReactGA.initialize(trackingId);

class App extends Component {
  constructor() {
    super();
    this.state= {
      list: ['Written in ReactJS', 'Always Under Construction', 'Hosted on Github', 'Outlook Hazy -- Try Again', 'Writing New Things as You Click', 'Trying to Figure Out What You Want'],
      item: '',
      start: true,
      end: false,
      counter: 0
    };
    this.changeItem = this.changeItem.bind(this);
  }
  changeItem(oldItem) {
    this.setState({item : oldItem});
    this.setState({start: false});
    let counterPlus= this.state.counter + 1;
    this.setState({counter: counterPlus});
    if(this.state.counter === 9){
      this.setState({end: true});
    }
  }
  render() {
    const items= this.state.list;
    let currently= '';
    let index= Math.floor(Math.random()*items.length);
    if(this.state.start){
      currently= items[0];
    } else if(this.state.end){
      currently= 'Done';
    } else {
      while(items[index] === this.state.item){
        index= Math.floor(Math.random()*items.length);
      }
      currently= items[index];
    }


    return (
		<div className="App">
			<header className="App-header">
				<div className="top-paper" alt="paper background">
					<HomeNav />
				</div>
				<aside className="headline-content">MicahRobinson.Design is <span className="currently" onClick={this.changeItem.bind(this, currently)}>{currently}</span></aside>
			</header>
      <section className="project-wrap">
        <ProjectFilter />
      </section>
      <section className="mid-paper">
        <Resume />
      </section>
      <footer className="bottom-area">
        <Social />
        <p>Micah Robinson © 2019</p>
      </footer>
		</div>
    );
  }
}

export default App;
